<!-- eslint-disable max-len -->
<template>
  <section class="px-3">
    <div class="row g-4">
      <div class="col-3">
        <div class="list-group">
          <template v-for="(item, index) in classList" :key="index">
            <a
              href="#"
              class="list-group-item list-group-item-action"
              :class="{ active: tempData.className === item.className }"
              aria-current="true"
              @click.prevent="tempData.className = item.className"
            >
              {{ item.className }}
            </a>
          </template>
          <button
            class="btn btn-heightlight w-100 d-flex justify-content-between text-light"
            @click="this.$refs.addClass.showModal()"
          >
            <span>建立班級</span><i class="bi bi-plus-lg"></i>
          </button>
        </div>
      </div>
      <div class="col-9">
        <div class="card text-center">
          <div class="card-header bg-primary">
            <ul class="nav nav-tabs card-header-tabs">
              <li class="nav-item">
                <a class="nav-link text-light" aria-current="true" href="#"
                  >群組</a
                >
              </li>
              <li class="nav-item">
                <a class="nav-link active" href="#">題庫</a>
              </li>
              <li class="nav-item">
                <a class="nav-link text-light" aria-disabled="true">Disabled</a>
              </li>
            </ul>
          </div>
          <!-- topic -->
          <div
            class="card-body overflow-y-scroll"
            style="height: 65vh"
            ref="items"
            id="items"
          >
            <template v-for="(item, index) in choseList" :key="index">
              <div
                class="card mb-4 shadow btn animate__animated animate__infinite headShake"
                :id="item.id"
              >
                <div class="row g-0">
                  <div class="col-md-4">{{ index + 1 }}</div>
                  <div class="col-md-8">
                    <div class="card-body">
                      <h5 class="card-title">Card title</h5>
                      <div class="card-text">
                        {{ item.title }}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </template>
          </div>
          <div class="card-footer">
            <button
              class="btn btn-outline-primary"
              @click="this.$refs.addTopic.showModal()"
            >
              新增題庫
            </button>
          </div>
        </div>
      </div>
    </div>
  </section>
  <AddClassModal ref="addClass" />
  <AddTopicModal ref="addTopic" />
</template>

<style lang="scss">
.animate__animated.animate__headShake {
  --animate-duration: 1.5s;
  --animate-delay: 0s;
}
</style>

<script>
// eslint-disable-next-line import/no-extraneous-dependencies
import Sortable from 'sortablejs';
import AddClassModal from '@/components/group/AddClassModal.vue';
import AddTopicModal from '@/components/group/AddTopicModal.vue';
import choseData from '@/assets/jsons/chose.json';
import classData from '@/assets/jsons/class.json';

export default {
  data() {
    return {
      sortable: '',
      choseList: choseData,
      classList: classData,
      tempData: {
        className: '3年2班',
      },
      newList: '',
    };
  },
  components: {
    AddClassModal,
    AddTopicModal,
  },
  methods: {},
  mounted() {
    this.sortable = Sortable.create(this.$refs.items, {
      animation: 500,
      onStart() {
        const headShakeList = Object.values(
          // eslint-disable-next-line comma-dangle
          document.getElementsByClassName('headShake')
        );
        headShakeList.forEach((item) => {
          item.classList.add('animate__headShake');
        });
      },
      onEnd() {
        const headShakeList = Object.values(
          // eslint-disable-next-line comma-dangle
          document.getElementsByClassName('headShake')
        );
        headShakeList.forEach((item) => {
          item.classList.remove('animate__headShake');
        });
        const tempList = document.getElementsByClassName('headShake');
        this.newList = [];
        Object.values(tempList).forEach((item) => {
          this.newList.push(item.id);
        });
        console.log(this.newList);
      },
    });
  },
};
</script>
